import hFormAlert from '@/components/h-form-alert/h-form-alert.vue'
export default {
	components: {
		hFormAlert
	},
	data() {
		return {
			withPeople:"",
			phone:"",
			email:"",
			cId: 1,
			modal6: false,
			jobData:{}
		}
	},
	methods: {
		// 预览或提交前的校验和保存到缓存
		save(){
			console.log("save开始")
			if(this.withPeople == ''){
				uni.showToast({
					icon:"none",
					title:"请输入负责人姓名"
				})
				return false
			}
			if(this.phone == ''){
				uni.showToast({
					icon:"none",
					title:"请输入联系电话"
				})
				return false
			}
			if(this.email == ''){
				uni.showToast({
					icon:"none",
					title:"请输入接收简历的邮箱"
				})
				return false
			}
			var _this = this
			uni.getStorage({
			    key: 'jobData',
			    success: function (res) {
			        console.log(res.data);
					_this.jobData = res.data
					_this.jobData.withPeople = _this.withPeople
					_this.jobData.phone = _this.phone
					_this.jobData.phone = _this.phone
					_this.jobData.email = _this.email
					_this.jobData.cId = _this.cId
					console.log(_this.jobData);
					uni.setStorage({
					    key: 'jobData',
					    data: _this.jobData,
					    success: function () {　　　　　　　　　
					    }
					}) 
			    }
			});
			return true
		},
		confirm(e){
			this.save()
			this.hide6()
		},
		cancel(e){
			this.hide6()
			uni.showToast({
				icon:"none",
				title:"你点击了取消"
			})
		},
		release() {
			if(this.save()){
				this.modal6 = true
			}
		},
		hide6() {
			this.modal6 = false
		},
		preview(e){
			if(this.save()){
				uni.navigateTo({
					url:"../../job-preview/job-preview"
				})
			}
		},
	}
}
